<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12">
        <v-row justify="center">
          <v-col cols="4">
            <v-autocomplete
              v-model="searchItem"
              :items="searchArray"
              item-text="value"
              item-value="npi"
              placeholder="Provider Name/Practice/Speciality"
              append-icon="magnify"
              prepend-inner-icon="search"
              outlined
              return-object
              hide-no-data
              :filter="filterSearchArray"
              dense
              hide-details
              @change="nameSearch"
            ></v-autocomplete>
          </v-col>
          <v-col cols="2">
            <v-combobox hide-details dense v-model="searchLocation" :items="locationList" placeholder="Zip Code" outlined clearable append-icon>
              <template slot="item" slot-scope="data">
                <v-icon>near_me</v-icon>
                {{ data.item }}
              </template>
            </v-combobox>
          </v-col>
          <v-col cols="2">
            <v-combobox
              dense
              hide-details
              v-model="searchLocationRange"
              :item-value="locationRangeList.value"
              :item-text="locationRangeList.text"
              :items="locationRangeList"
              placeholder="Range"
              outlined
            ></v-combobox>
          </v-col>
          <v-col cols="2">
            <v-btn block @click="search">Search</v-btn>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col cols="2">
            <v-combobox hide-details dense v-model="searchGender" :item-text="genderList.text" :items="genderList" placeholder="Gender" outlined></v-combobox>
          </v-col>
          <v-col cols="3">
            <v-checkbox v-model="searchStatVisits" label="Stat Visits Accepted" dense hide-details></v-checkbox>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <div v-if="displayResults">
      <v-layout row wrap>
        <div style="color:rgba(36,32,44,.5); margin-bottom: 15px;">Search Results: {{ selectedItems.length }} record(s) found.</div>
      </v-layout>
      <v-layout row wrap v-for="provider in selectedItems" v-bind:key="provider.npi" class="mb-5 pa-3 provider-row">
        <v-col cols="12" sm="12" md="6">
          <h3 @click="showProvider(provider.NPI)" class="link">
            {{ provider.Prov_Name + ", " + provider.Degree }}
            <v-btn small color="#49157c" dark>VIEW FULL BIO</v-btn>
          </h3>
          <div class="mb-3">{{ provider.Speciality }}</div>
          <h4>Hospital Affiliations</h4>
          <div v-for="(aff, index) in provider.Affiliations" v-bind:key="index">
            <div>{{ aff.Hospital_Name }}</div>
          </div>
        </v-col>
        <v-col cols="12" sm="6" md="2">
          <!-- <v-img class="results" contain height="auto" width="150" v-bind:src="`https://www.chsli.org/sites/default/files/md-photos/${provider.NPI}.jpg`"></v-img> -->
          <v-img class="results" contain height="auto" width="150" v-bind:src="`${imagePath}/${provider.NPI}.jpg`"></v-img>
        </v-col>
        <v-col cols="12" sm="6" md="4">
          <div v-for="(address, index) in provider.Location" v-bind:key="index">
            <v-card :class="cardDisplay(index) ? 'hidden-image hidden' : 'hidden-image show'" style="margin-bottom: 5px;">
              <!-- :class="cardDisplay(index)" -->
              <v-card-text>
                <div>{{ address.Practice }}</div>
                <div>{{ address.Address }}</div>
                <div>{{ address.City + ", " + address.state + " " + address.ZIP }}</div>
                <div v-if="address.Phone">
                  <span class="material-icons" style="vertical-align: middle;">call</span>
                  {{ address.Phone }}
                </div>
                <div v-if="address.Fax">
                  <span class="material-icons" style="vertical-align: middle;">print</span>
                  {{ address.Fax }}
                </div>
              </v-card-text>
            </v-card>
            <div v-if="provider.Location.length > 1 && index === provider.Location.length - 1">
              <v-btn text small @click="showLocations(provider.Location.length)">
                Show All Locations
                <span class="material-icons">keyboard_arrow_down</span>
              </v-btn>
            </div>
          </div>
        </v-col>
      </v-layout>
      <v-btn v-scroll="onScroll" v-show="fab" fab dark fixed bottom right color="primary" @click="toTop">
        <v-icon>keyboard_arrow_up</v-icon>
      </v-btn>
    </div>
  </v-container>
</template>

<script>
import router from "../router";
const axios = require("axios");
const gKey = "AIzaSyBoBoVxHwq1bcVmqS5QGFyte0uFF051_a4";
import Config from "../config";

export default {
  name: "CHSoogle",
  data: () => {
    return {
      runDateTime: null,
      fab: false,
      selectedItems: [
        {
          lastname: null,
          firstname: null,
          title: null,
          specialty: null,
        },
      ],
      // searchItem: { NPI: null, value: null },
      searchItem: { NPI: "1790762623", value: "Aaron, Michael L" },
      // searchItem: { practice: true, value: "Cardiology" },
      searchLocation: null,
      searchLocationRange: null,
      searchPractice: null,
      searchGender: null,
      searchStatVisits: null,
      displayResults: false,
      hidden: true,
      locationList: ["Current Location"],
      locationRangeList: [
        { value: 5, text: "5 Miles" },
        { value: 15, text: "15 Miles" },
        { value: 25, text: "25 Miles" },
        { value: 50, text: "50 Miles" },
      ],
      genderList: [
        { abbr: "F", text: "Female" },
        { abbr: "M", text: "Male" },
        { abbr: "No Preference", text: "No Preference" },
      ],
      imagePath: Config.imageUrl,
    };
  },
  computed: {
    providerData: function() {
      return this.$store.getters.PROVIDER_LIST;
    },
    searchArray: function() {
      // populating the autocomplete box
      let tmpArray1 = [];
      for (let i = 0; i < this.providerData.length; i++) {
        tmpArray1.push({
          NPI: this.providerData[i].NPI,
          value: this.providerData[i].Prov_Name,
        });
      }
      tmpArray1 = tmpArray1.sort((t1, t2) => (t1.value < t2.value ? -1 : 1));

      let tmpArray2 = [];
      for (let i = 0; i < this.providerData.length; i++) {
        tmpArray2.push({
          specialty: true,
          value: this.providerData[i].Speciality,
        });
      }
      tmpArray2 = Array.from(new Set(tmpArray2.map((a) => a.value)))
        .map((value) => {
          return tmpArray2.find((a) => a.value === value);
        })
        .sort((t1, t2) => (t1.value < t2.value ? -1 : 1));

      let tmpPractices = [];
      for (let i = 0; i < this.providerData.length; i++) {
        if (this.providerData[i].Location) {
          for (let j = 0; j < this.providerData[i].Location.length; j++) {
            tmpPractices.push({
              practice: true,
              value: this.providerData[i].Location[j].Practice,
            });
          }
        }
      }
      tmpPractices = Array.from(new Set(tmpPractices.map((a) => a.value)))
        .map((value) => {
          return tmpPractices.find((a) => a.value === value);
        })
        .sort((t1, t2) => (t1.value < t2.value ? -1 : 1));

      let tmpArray = [];
      tmpArray.push({ header: "Providers" });
      tmpArray = tmpArray.concat(tmpArray1);
      tmpArray.push({ divider: true });
      tmpArray.push({ header: "Specialty" });
      tmpArray = tmpArray.concat(tmpArray2);
      tmpArray.push({ divider: true });
      tmpArray.push({ header: "Practice" });
      tmpArray = tmpArray.concat(tmpPractices);

      return tmpArray;
    },
  },
  watch: {},
  methods: {
    showProvider: function(selectedNpi) {
      console.log("selectedNpi - ", selectedNpi);
      router.push({ name: "provider", params: { npi: selectedNpi } });
    },
    filterSearchArray: function(obj, qText, iText) {
      // filter the search box of provider names and practices as they type
      if (obj.header || obj.divider) {
        return true;
      }
      if (obj.NPI && obj.value.toLowerCase().includes(qText.toLowerCase())) {
        return true;
      }
      if ((obj.specialty || obj.practice) && obj.value.toLowerCase().includes(qText.toLowerCase())) {
        return true;
      }
    },
    filterNames: function(item) {
      // find the object(s) based on whats in the name/practice search box
      if (item.NPI) {
        return this.providerData.filter((obj) => obj.NPI == item.NPI);
      }

      if (item.specialty) {
        return this.providerData.filter((obj) => obj.Speciality == item.value);
      }

      return Array.from(this.providerData); // all records
    },
    nameSearch: function(sel) {
      if (sel.NPI) {
        this.selectedItems = this.providerData.filter((obj) => obj.NPI == sel.NPI);
        this.displayResults = true;
      }
    },
    search: async function() {
      console.log("searching...");
      this.selectedItems = [...this.providerData];

      // name/specialty/practice name filter
      if (this.searchItem.value) {
        this.selectedItems = this.filterNames(this.searchItem);
      }

      // gender
      if (this.searchGender && this.searchGender.text != "No Preference") {
        this.selectedItems = this.selectedItems.filter((obj) => {
          if (obj.Gender) {
            return obj.Gender.toLowerCase() === this.searchGender.abbr.toLowerCase();
          }
        });
      }

      // stat visits accepted
      if (this.searchStatVisits) {
        console.log("searchStatVisits - ", this.searchStatVisits);
        this.selectedItems = this.selectedItems.filter((obj) => obj.stat_visit === "true");
      }

      // location
      if (this.searchLocation) {
        // trying geolocation search
        let uCoords = {};

        if (this.searchLocation === "Current Location") {
          const uPos = await getPosition();

          uCoords.lat = uPos.coords.latitude;
          uCoords.lng = uPos.coords.longitude;
        } else {
          const gCode = await axios.get(`https://maps.googleapis.com/maps/api/geocode/json?address=${this.searchLocation}&key=${gKey}`);

          if (gCode.data.status != "OK") {
            console.log("gCode - ", gCode);
          }
          uCoords.lat = gCode.data.results[0].geometry.location.lat;
          uCoords.lng = gCode.data.results[0].geometry.location.lng;
        }

        this.selectedItems = this.selectedItems.filter((item) => {
          for (let j = 0; j < item.Location.length; j++) {
            const pLoc = item.Location[j];
            if (pLoc.lat && pLoc.lng) {
              const range2 = distance(uCoords.lat, uCoords.lng, pLoc.lat, pLoc.lng, "M");
              console.log("range2 - ", range2);
              if (range2 < 16) {
                return true;
              }
              return false;
            }
          }
        });
      }

      // sort alphabetically by provider name
      this.selectedItems.sort((t1, t2) => (t1.Prov_Name < t2.Prov_Name ? -1 : 1));

      console.log("end of search");
      this.displayResults = true;
    },
    cardDisplay: function(indx) {
      // only want to show the 1st location if there are more than 1
      if (indx > 0) {
        return `hidden-image ${this.hidden}`;
      }
    },
    showLocations: function(len) {
      console.log("show location");
      this.hidden = "show";
    },
    onScroll(e) {
      if (typeof window === "undefined") return;
      const top = window.pageYOffset || e.target.scrollTop || 0;
      this.fab = top > 20;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
  },
};

var getPosition = function(options) {
  return new Promise(function(resolve, reject) {
    navigator.geolocation.getCurrentPosition(resolve, reject, options);
  });
};

function distance(lat1, lon1, lat2, lon2, unit) {
  // https://www.geodatasource.com/developers/javascript
  if (lat1 == lat2 && lon1 == lon2) {
    return 0;
  } else {
    var radlat1 = (Math.PI * lat1) / 180;
    var radlat2 = (Math.PI * lat2) / 180;
    var theta = lon1 - lon2;
    var radtheta = (Math.PI * theta) / 180;
    var dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
    if (dist > 1) {
      dist = 1;
    }
    dist = Math.acos(dist);
    dist = (dist * 180) / Math.PI;
    dist = dist * 60 * 1.1515;
    if (unit == "K") {
      dist = dist * 1.609344;
    }
    if (unit == "N") {
      dist = dist * 0.8684;
    }
    return dist;
  }
}
</script>

<style>
.link {
  cursor: pointer;
}
.provider-row {
  background-color: #f1ebe5;
}
.v-input--checkbox {
  margin-top: 5px !important;
}
.v-image {
  float: right;
}
.hidden-image.hidden {
  display: none !important;
}
.hidden-image.show {
  display: block !important;
}

@media only screen and (max-width: 960px) {
  .results.v-image {
    float: left;
  }
}

@media only screen and (max-width: 600px) {
  .v-image {
    float: left;
  }
}
</style>
