const axios = require("axios");
import Config from "../../config";

const defaultState = {
  providers: [],
  dataDate: null,
};
const getters = {
  PROVIDER_LIST: (state) => {
    return state.providers;
  },
  DATA_DATE: (state) => {
    return state.dataDate;
  },
};
const mutations = {
  SET_PROVIDER_LIST: (state, payload) => {
    state.providers = payload;
  },
  SET_DATA_DATE: (state, payload) => {
    state.dataDate = payload;
  },
};
const actions = {
  FETCH_PROVIDER_LIST: async (context) => {
    const json = await axios.get(`${Config.dataFile}`);
    //    const json = await axios.get(`/wadetest/chspp-provider-directory/data/providers.json`);
    context.commit("SET_PROVIDER_LIST", json.data[0].providerData);
    context.commit("SET_DATA_DATE", json.data[0].runDateTime);
  },
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions,
};
